html {
  height: 100%;
}
html, body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

body {
  background: -webkit-gradient(linear, left top, left bottom, from(white), color-stop(rgb(238, 247, 249)), color-stop(rgb(193, 223, 242)), color-stop(#BEE2F8), color-stop(rgb(154, 190, 212)), to(rgba(54, 88, 124)));
  background: -webkit-linear-gradient(top, white, rgb(238, 247, 249), rgb(193, 223, 242), #BEE2F8, rgb(154, 190, 212), rgba(54, 88, 124));
  background: -o-linear-gradient(top, white, rgb(238, 247, 249), rgb(193, 223, 242), #BEE2F8, rgb(154, 190, 212), rgba(54, 88, 124));
  background: linear-gradient(to bottom, white, rgb(238, 247, 249), rgb(193, 223, 242), #BEE2F8, rgb(154, 190, 212), rgba(54, 88, 124));
  min-height: 100%;
}


