.contact {
   font-size: 35px;
   position: absolute;
   margin-left: 5rem;
   top: 20%;
   padding: 25px;
   background: url('border.png') no-repeat;
   background-size: 715px 560px;
   font-family: Orbitron;
}
form {
   display: -ms-flexbox;
   display: flex;
   -ms-flex-direction: column;
       flex-direction: column;
   padding: 30px;
   background: -webkit-gradient(linear, left top, left bottom, from(#29354286), color-stop(rgba(54, 88, 124, 0.155)), color-stop(transparent), color-stop(transparent), color-stop(transparent), color-stop(transparent), to(transparent));
   background: -webkit-linear-gradient(#29354286, rgba(54, 88, 124, 0.155), transparent, transparent, transparent, transparent, transparent);
   background: -o-linear-gradient(#29354286, rgba(54, 88, 124, 0.155), transparent, transparent, transparent, transparent, transparent);
   background: linear-gradient(#29354286, rgba(54, 88, 124, 0.155), transparent, transparent, transparent, transparent, transparent); 
}
label {
   font-size: 20px;
   margin-left: 5px;
   margin-bottom: 5px;
   color: #414655;

}
.contact-header {
  background: -webkit-gradient(linear, left top, left bottom, from(#293542), color-stop(#36587C), to(#293542));
  background: -webkit-linear-gradient(#293542, #36587C, #293542);
  background: -o-linear-gradient(#293542, #36587C, #293542);
  background: linear-gradient(#293542, #36587C, #293542);
  height: 80px;
  padding: 40px 20px 10px 20px;
  margin: 0;
  width: 625px;
}
.input-name, .input-comment {
   width: 550px;
   font-size: 15px;
   border: 1px solid grey;
   border-radius: 10px;
   padding: 10px;
   outline: none;
}
.input-comment {
   height: 120px;
}
.submit {
   border: none;
   width: 78px;
   margin-left: 490px;
   font-weight: bold;
   font-size: 20px;
   padding: 10px;
   color: #414655;
   font-family: Orbitron;
}

.submit:hover {
  background: url('blueborder.png') no-repeat;
  background-size: 75px 35px;
}

@media screen and (max-height: 750px) {
  .contact {
    top: 10%;
    margin-left: 0;
    background-size: 397px 480px;
    height: 650px;
  }
  .contact-header {
    font-size: 40px;
    width: 310px;
    height: 40px;
    padding: 23px 20px 15px 20px;
  }
  form {
    padding: 25px;
    width: 300px;
  }
  .input-name, .input-comment {
    width: 280px;
  }
  .input-comment {
    height: 150px;
 }
}


